.btn-switch{display:inline-block;vertical-align:middle
}
.btn-switch .switch{position:relative;display:inline-block;width:52px;height:24px
}
.btn-switch .switch input{display:none
}
.btn-switch .slider{position:absolute;cursor:pointer;top:0;left:0;right:0;bottom:0;background-color:#acacac;-webkit-transition:.4s;transition:.4s
}
.btn-switch .slider:before{position:absolute;content:"";height:18px;width:18px;left:4px;bottom:3px;background-color:#fff;-webkit-transition:.4s;transition:.4s
}
.btn-switch input:checked+.slider{background-color:#47b200
}
.btn-switch input:focus+.slider{-webkit-box-shadow:0 0 1px #2196f3;box-shadow:0 0 1px #2196f3
}
.btn-switch input:checked+.slider:before{transform:translateX(27px);-webkit-transform:translateX(27px);-moz-transform:translateX(27px);-ms-transform:translateX(27px);-o-transform:translateX(27px)
}
.btn-switch .on{display:none
}
.btn-switch .on,.btn-switch .off{color:#fff;position:absolute;font-size:10px;font-family:"Lato-Regular"
}
.btn-switch .on{-webkit-transform:translate(-20%, -50%);transform:translate(-20%, -50%);top:50%;left:20%
}
.btn-switch .off{-webkit-transform:translate(-10%, -50%);transform:translate(-10%, -50%);top:50%;right:10%
}
.btn-switch input:checked+.slider .on{display:block
}
.btn-switch input:checked+.slider .off{display:none
}
.btn-switch .slider.round{border-radius:34px;height:24px
}
.btn-switch .slider.round:before{border-radius:50%
}
.popup-two-step .btn-switch{padding-top:20px
}
.popup-two-step .btn-switch .switch{width:52px
}
.popup-two-step .btn-switch .off{left:60%
}
.popup-two-step .btn-switch input:checked+.slider:before{-webkit-transform:translateX(85px);transform:translateX(85px)
}
